@import url('https://fonts.googleapis.com/css2?family=Kaisei+HarunoUmi&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&family=Quicksand:wght@300&display=swap');

* {
  margin: 0;
  box-sizing: border-box;
}

html{
  background-color: black;
}

.layout {
  height: 110vh;
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Kaisei HarunoUmi', serif;
  font-size: 13px;
}

.historia {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  color: white;
  border: 2px solid white;
  padding: 10px;
}

.opciones {
  color: white;
  font-size: 12px;
}

.opcion {
  display: flex;
  gap: 15px;
  margin-bottom: 10px;
  font-family: 'Quicksand', sans-serif;
}

.recordatorio {
  color: white;
  margin-top: 60px;
  font-family: 'Quicksand', sans-serif;
  font-weight: bold;
}

.botones {
  box-shadow: inset 0px 1px 0px 0px #ffffff;
  background: linear-gradient(to bottom, #f9f9f9 5%, #e9e9e9 100%);
  background-color: #f9f9f9;
  border-radius: 6px;
  border: 1px solid #dcdcdc;
  display: inline-block;
  cursor: pointer;
  color: #000000;
  font-family: Helvetica, sans-serif;
  font-size: 15px;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #ffffff;
}
.botones:hover {
  background: linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
  background-color: #e9e9e9;
}
.botones:active {
  position: relative;
  top: 1px;
}